<template>
    <div class="orderWarp">
        <header>
            <router-link to="confiemorder">
                <i class="el-icon-arrow-left"></i>
            </router-link>
            <section>
                <span>订单列表</span>
            </section>
        </header>
        <ul class="order_list_ul">
            <li class="order_list_li" v-for="(list,index) in listArr" :key="list.id">
                <img :src="'https://elm.cangdu.org/img/'+list.restaurant_image_url" alt="" class="restaurant_image">
                <section class="order_item_right">
                    <section @click="goOrder(index,list)">
                        <div class="order_item_right_header">
                            <div class="order_header">
                                <h4>
                                    <span class="ellipsis">{{list.restaurant_name}}</span>
                                    <i class="el-icon-arrow-right" style="font-size: .6rem;color: #ccc;margin-right: .2rem"></i>
                                </h4>
                                <p class="order_time">{{list.formatted_created_at}}</p>
                            </div>
                            <p class="order_status" v-if="isShow1">支付超时</p>
                            <p class="order_status" v-if="isShow">等待支付</p>
                        </div>
                        <div class="order_basket">
                            <p class="order_name ellipsis">考研加油 等2件商品</p>
                            <p class="order_amount">¥{{list.total_amount}}.00</p>
                        </div>
                    </section>
                    <div class="order_again">
                        <span class="buy_again" v-if="isShow1">再来一单</span>
                        <span class="rem_time" v-if="isShow">去支付(还剩{{Minute}}分{{Second}}秒)</span>
                    </div>
                </section>
            </li>
        </ul>
        <div class="footer">
            <router-link to="/msite">
                <!--<section class="footer_list">-->
                <i class="el-icon-eleme icon_style"></i>
                <span>外卖</span>
                <!--</section>-->
            </router-link>
            <router-link to="/search">
                <!--<section class="footer_list">-->
                <i class="el-icon-discover icon_style"></i>
                <span>搜索</span>
                <!--</section>-->
            </router-link>
            <router-link to="/order">
                <!--<section class="footer_list">-->
                <i class="el-icon-s-fold icon_style"></i>
                <span>订单</span>
                <!--</section>-->
            </router-link>
            <router-link to="/profile">
                <!--<section class="footer_list">-->
                <i class="el-icon-s-custom icon_style"></i>
                <span>我的</span>
                <!--</section>-->
            </router-link>

        </div>
        <!--弹框-->
        <div class="alet_container" v-if="isShow_Tan">
            <section class="tip_text_container">
                <div class="tip_icon">
                    <span></span>
                    <span></span>
                </div>
                <p class="tip_text">支付超时</p>
                <div class="confrim" @click="tanBtn">确定</div>
            </section>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Order",
        data(){
            return {
                isShow1:false,
                isShow:true,
                listArr:[],
                Second: 60,//秒
                Minute: 15,//分
                isShow_Tan:false,
            }
        },
        created(){
            this.user_id = localStorage.getItem("user_id")
             this.axios.get("/bos/v2/users/"+ this.user_id +  "/orders?limit=10&offset=0").then(res=>{
                 console.log(res);
                 this.listArr = res;
             }).catch(err=>{})

            let countdown = setInterval(() => {
                if (this.Second !== 0) {
                    this.Second--;
                    if(this.Second===0&&this.Minute!==0){
                        this.Second = 60;
                        this.Minute--;
                    }
                }
                if (this.Minute <= 0&&this.Second===0) {
                    clearInterval(countdown);
                    //弹出提示框
                    this.isShow_Tan = true;
                }
            }, 1000)
        },
        methods:{
            goOrder(index,list){
                console.log(index)
                console.log(list.id)
                localStorage.setItem("dingId",list.id)
                this.$router.push({path:'/orderDetail'})
            },
            tanBtn(){
                this.isShow = false;
                this.isShow1 = true;
            }
        }
    }
</script>

<style scoped>
    .orderWarp{
        padding-top: 1.95rem;
        /*background-color: azure;*/
    }
    header {
        background-color: #3190e8;
        position: fixed;
        z-index: 100;
        left: 0;
        top: 0;
        width: 100%;
        height: 1.95rem;
    }
    header>a{
        position: absolute;
        top: 50%;
        left: .4rem;
        transform: translateY(-50%);
        color: #fff;
    }
    header>section{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        width: 50%;
        color: #fff;
        text-align: center;
    }
    header>section span{
        font-size: .8rem;
        color: #fff;
        text-align: center;
        font-weight: 700;
    }

    .order_list_ul{
        padding-top: .5rem;
    }
    .order_list_li{
        background-color: #fff;
        display: flex;
        margin-bottom: .5rem;
        padding: .6rem .6rem 0;
    }
    .restaurant_image{
        width: 2rem;
        height: 2rem;
        margin-right: .4rem;
    }
    .order_item_right{
        flex: 5;
    }
    .order_item_right_header{
        border-bottom: .025rem solid #f5f5f5;
        padding-bottom: .3rem;
        display: flex;
        justify-content: space-between;
    }
    .order_header{

    }
    .order_header h4{
        display: flex;
        align-items: center;
        justify-content: flex-start;
        font-size: .75rem;
        color: #333;
        line-height: 1rem;
        width: 9rem;
    }
    .order_time{
        font-size: .55rem;
        color: #999;
        line-height: .8rem;
    }
    .order_status{
        font-size: .6rem;
        color: #333;
    }


    .order_basket{
        display: flex;
        justify-content: space-between;
        line-height: 2rem;
        border-bottom: .025rem solid #f5f5f5;
    }
    .order_name{
        font-size: .6rem;
        color: #666;
        width: 10rem;
    }
    .ellipsis{
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }
    .order_amount{
        font-size: .6rem;
        color: #f60;
        font-weight: 700;
    }

    .order_again{
        text-align: right;
        line-height: 1.6rem;
    }
    .buy_again{
        font-size: .55rem;
        color: #3190e8;
        border: .025rem solid #3190e8;
        padding: .1rem .2rem;
        border-radius: .15rem;
    }
    .rem_time{
        font-size: .55rem;
        /*color: orange;*/
        padding: .1rem .2rem;
        border-radius: .15rem;
        color: orange;
        border-width: 1px;
        border-style: solid;
        border-color: orange;
    }

    .footer{
        background-color: #fff;
        position: fixed;
        z-index: 100;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 1.95rem;
        display: flex;
        box-shadow: 0 -0.02667rem 0.05333rem rgba(0,0,0,.1);
    }
    .footer a{
        text-decoration: none;
        flex: 1;
        display: flex;
        text-align: center;
        flex-direction: column;
        align-items: center;
        color:#666 ;
    }
    .footer a span{
        padding-top:.3rem;
        font-size: .45rem;
        color: #666;
    }
    .icon_style{
        width: .8rem;
        height: .8rem;
        margin-top: .3rem;
        fill: #ccc;
    }


    .alet_container{
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 200;
    }
    .tip_text_container{
        position: absolute;
        top: 50%;
        left: 50%;
        margin-top: -6rem;
        margin-left: -6rem;
        width: 12rem;
        animation: tipMove .4s;
        background-color: #fff;
        padding-top: .6rem;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        border: 1px;
        border-radius: .25rem;
    }
    .tip_icon{
        width: 3rem;
        height: 3rem;
        border: .15rem solid #f8cb86;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
    .tip_icon>:nth-child(1){
        width: .12rem;
        height: 1.5rem;
        background-color: #f8cb86;
    }
    .tip_icon>:nth-child(2){
        width: .2rem;
        height: .2rem;
        border: 1px;
        border-radius: 50%;
        margin-top: .2rem;
        background-color: #f8cb86;
    }
    .tip_text{
        font-size: .7rem;
        color: #333;
        line-height: .9rem;
        text-align: center;
        margin-top: .8rem;
        padding: 0 .4rem;
    }
    .confrim{
        font-size: .8rem;
        color: #fff;
        font-weight: 700;
        margin-top: .8rem;
        background-color: #4cd964;
        width: 100%;
        text-align: center;
        line-height: 1.8rem;
        border: 1px;
        border-bottom-left-radius: .25rem;
        border-bottom-right-radius: .25rem;
    }
</style>